import React from 'react';
import PropTypes from 'prop-types';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import MobileDialog from './ResponsiveDialog';


const styles = theme => ({
  body: {
    marginLeft : 50,
    marginRight: 50,
    marginBottom: 100
    },
  card: {
    display: 'flex',

    },
  details: {
    display: 'flex',
    flexDirection: 'row',
  },
  content: {
    display: 'flex',
    flexDirection: 'column',
  },
  media: {
    minWidth: 150,
    minHeight: 125
    },
  title: {
    background: 'grey',
    align: 'center'
  },
  heroContent: {
    maxWidth: 600,
    margin: 'auto',
    //padding: `${theme.spacing(8)}px 0 ${theme.spacing(6)}px`,
    padding: `${theme.spacing(8)} 10 ${theme.spacing(6)} 10`,

  },
  cardHeader: {
    backgroundColor: theme.palette.grey[200],
  },
})


class Offre extends React.Component {
  constructor(props) {
    super(props);
    this.state = {expanded: false, open1: false, open2: false, open3: false};

    // This binding is necessary to make `this` work in the callback
    this.handleExpandClick = this.handleExpandClick.bind(this);
    this.handleOpenClick1 = this.handleOpenClick1.bind(this);
    this.handleOpenClick2 = this.handleOpenClick2.bind(this);
    this.handleOpenClick3 = this.handleOpenClick3.bind(this);
    this.handleClose      = this.handleClose.bind(this);
  }

  handleExpandClick() {
    this.setState(state => ({expanded: !state.expanded}));
  };

  handleOpenClick1 = () => {
    this.setState({open1: true});
  };
  handleOpenClick2 = () => {
    this.setState({open2: true});
  };
  handleOpenClick3 = () => {
    this.setState({open3: true});
  };

  handleClose = () => {
    this.setState({open1: false, open2: false, open3: false});
  };


  render () {
    const {classes} = this.props;

    return (<div className={classes.body}>

          <Typography component="h1" variant="h2" align="center" color="textPrimary" gutterBottom>
            Voir les données pour décider
          </Typography>

          <div>
          <Grid spacing={4} container alignItems="stretch">
              <Grid item  xs={12} sm={12} md={4}>
                <Card className={classes.card} elevation={8}>
                  <CardActionArea onClick={this.handleOpenClick1}>
                    <div className={classes.details}>
                      <CardMedia className={classes.media}
                        image= '/dataviz.png'
                      />
                      <CardContent className={classes.content}>
                        <Typography component="h6" variant="h6">
                          DATAVIZ
                        </Typography>
                        <Typography variant="subtitle1" color="textSecondary">
                          Explorer, Visualiser, Analyser, Comprendre
                        </Typography>
                      </CardContent>
                    </div>
                  </CardActionArea>
                <MobileDialog 

                  open={this.state.open1}
                  onClose={this.handleClose}
                  title="DATAVIZ"
                  text="Les données rendues visibles permettent de découvrir des vérités insoupçonnées et de répondre aux questions des utilisateurs"
                  link="/blog/data"
                >
                </MobileDialog>
                </Card>

              </Grid>

              <Grid item  xs={12} sm={12} md={4} >
                <Card className={classes.card} elevation={8}>
                  <CardActionArea onClick={this.handleOpenClick2}>
                    <div className={classes.details}>
                      <CardMedia className={classes.media}
                        image= '/dashboard.png'
                      />
                      <CardContent className={classes.content}>
                        <Typography component="h6" variant="h6">
                          DASHBOARD
                        </Typography>
                        <Typography variant="subtitle1" color="textSecondary">
                          Mesurer, Expliquer, Piloter, Améliorer
                        </Typography>
                      </CardContent>
                  </div>
                  </CardActionArea>
                </Card>
                <MobileDialog 

                  open={this.state.open2}
                  onClose={this.handleClose}
                  title="DASHBOARD"
                  text="Le tableau de bord efficace est constitué des indicateurs clés qui regardent vers l'avant. Comment les choisir ? Les représenter ? "
                  link="/about"
                />
              </Grid>

              <Grid item  xs={12} sm={12} md={4}>
                <Card className={classes.card} elevation={8}>
                  <CardActionArea onClick={this.handleOpenClick3}>
                    <div className={classes.details}>
                      <CardMedia className={classes.media}
                        image= '/applications.png'
                      />
                      <CardContent className={classes.content}>
                        <Typography component="h6" variant="h6">
                          APPLICATION
                        </Typography>
                        <Typography variant="subtitle1" color="textSecondary">
                          Partager, Interroger, Communiquer, Suivre
                        </Typography>
                      </CardContent>
                    </div>
                  </CardActionArea>
                </Card>
                <MobileDialog

                  open={this.state.open3}
                  onClose={this.handleClose}
                  title="APPLICATION"
                  text="Avoir toujours disponible l'application légère (web, tablette et téléphone) qui donne accès à vos indicateurs et tableaux de bord"
                  link="/blog/spa/"
                />
              </Grid>
          </Grid>
          </div>

      </div>)
  
  };
}

Offre.propTypes = {
  classes: PropTypes.object.isRequired,
};
//export default Offre;
export default withStyles(styles)(Offre);