import React from 'react';


//import classNames from 'classnames';
//import PropTypes from 'prop-types';

//import CssBaseline from '@material-ui/core/CssBaseline';

import {Link} from '@reach/router'

import Button from '@material-ui/core/Button';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import withMobileDialog from '@material-ui/core/withMobileDialog';

//import Typography from '@material-ui/core/Typography';
//import { withStyles } from '@material-ui/core/styles';

class ResponsiveDialog extends React.Component {


  render () {
    const {fullScreen, link} = this.props;
    const open=this.props.open;
    return (
      <Dialog
            open={open}
            fullScreen={fullScreen}
            aria-labelledby="responsive-dialog-title"
          >
        <DialogTitle id="responsive-dialog-title">{this.props.title}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            {this.props.text}
          </DialogContentText>
          <DialogContentText>
          <br/>

            <Link component={Link} to={link}>
            En savoir plus
            </Link>
          </DialogContentText>
          <br/>
          <DialogContentText>
            Contacter : &nbsp;
            <a href={"mailto:alain.roan@perceptible.fr"} target="_blank" rel="noopener noreferrer">
              perceptible
            </a>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.props.onClose} color="primary">
            OK
          </Button>
        </DialogActions>
      </Dialog>)
  }
} 

export default withMobileDialog({breakpoint: 'xs'})(ResponsiveDialog); 